import React from "react";
import Main from "./Screan/main";
import Login from "./Screan/login";
import Post from "./Screan/map";
import Eror from "./Screan/Eror";
import Dext from "./Screan/text";

import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';
import Mainstack from "./bundele/news";
import sendPushNotification from "./Screan/noti"
import { gstyles } from "./gstyle";
const Stack = createStackNavigator();



export default function Navigate() {
    return <NavigationContainer>
        <Stack.Navigator>                
            
            <Stack.Screen
            name="–ì–æ–ª–æ–≤–Ω–∞"
            component={Main}
            optsions={{title:"–ì–æ–ª–æ–≤–Ω–∞"}}
            />         
            <Stack.Screen
            name="Login"
            component={Login}
            optsions={{title:"Login"}}
            />                    
           <Stack.Screen
            name="Dext"
            component={Dext}
            optsions={{title:"Dext"}}
            />
            <Stack.Screen
            name="main"
            component={Mainstack}
            optsions={{title:"main"}}
            />
            <Stack.Screen
            name="Eror"
            component={Eror}
            optsions={{title:"Eror"}}
            />
           <Stack.Screen
            name="–ó–∞–ø–æ—Å—Ç–∏—Ç–∏"
            component={Post}
            optsions={{title:"–ó–∞–ø–æ—Å—Ç–∏—Ç–∏"}}
            />
                       <Stack.Screen
            name="noti"
            component={sendPushNotification}
            optsions={{title:"noti" }}
            />
        </Stack.Navigator>
    </NavigationContainer>
}

